import C from './footer.module.scss'
import logo from './media/logo-image.png'
import vk from './media/vk.svg'
import telegram from './media/telegram.svg'
import insta from './media/insta.svg'
import fb from './media/fb.svg'
import { CustomSelect } from '../header'
import { ReactSVG } from 'react-svg'

export function Footer() {
	return (
		<footer>
			<section className={C.all}>
				<div>
					<img src={logo} alt="Logo" className={C.image}/>
					<CustomSelect dark={true} />
					<div className={C.social}>
						<ReactSVG src={insta}/>
						<ReactSVG src={telegram}/>
						<ReactSVG src={vk}/>
						<ReactSVG src={fb}/>
					</div>
					<div className={C.link}>
						<button> </button>
					</div>					
				</div>
				<nav>
					<div>
						<h2></h2>
						<ul>
							<li><button></button></li>
							<li><button></button></li>
							<li><button></button></li>
							<li><button></button></li>
							<li><button></button></li>
							<li><button></button></li>
							<li><button></button></li>
						</ul>
					</div>
					<div>
						<h2> </h2>
						<ul>
							<li><button> </button></li>
							<li><button> </button></li>
							<li><button>  </button></li>
							<li><button></button></li>
							<li><button> </button></li>
							<li><button></button></li>
						</ul>
					</div>
					<div>
						<h2> </h2>
						<ul>
							<li><button> </button></li>
							<li><button>  </button></li>
							<li><button></button></li>
							<li><button>  </button></li>
						</ul>
					</div>
				</nav>
			</section>
			<section className={C.copyright}>
				 {window.location.hostname}, 2022-{new Date().getFullYear()}
			</section>
        </footer>	
	)
}


